
body{
  background-color: #f8f8ff;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  color: #2c3e50;
}
.nav{
    position: fixed;
    width: 108px;
    left: 40px;
}
.nav ul{
  list-style: none;
  margin: 0;
  padding: 0;
}
.nav ul li{
  width: 108px;
  height: 48px;
  line-height: 48px;
  border:1px solid #dadada;
  text-align: center;
}
.nav ul li a{
  text-decoration: none;
  color: #999;
}
.main{
    height: 400px;
    margin-left: 180px;
    margin-right: 25px;
}



